<template>
  <div class="main">
    <bigControl></bigControl>
    <div id="screen" class="screen"></div>
  </div>
</template>

<script>
import { onMounted } from 'vue';
import ZThree from '../three/ZThree';
import * as THREE from 'three';
import { loaderModel } from '../three/loaderModel';
import bigControl from '../components/bigControl.vue';
import TWEEN from 'three/examples/jsm/libs/tween.module.js';
import { CSS3DRenderer } from 'three/examples/jsm/renderers/CSS3DRenderer.js';
import { cssRender } from '../three/cssRender';
import { createReprocessing } from '../three/reprocessing';
import { darkMaterial } from '../three/material';
let app,camera,scene,renderer,controls,clock,reprocessing,materials={};
import { secondaryTexture, trunkTexture, primaryTexture } from '@/three/texture';

export default {
  name: 'Home',
  components: {
    bigControl
  },
  setup() {
    const initZThree = async () => {
      app = new ZThree('screen')
      app.initZThree()
      // app.initHelper()
      app.initControl()
      app.initAmbientLight()
      app.initDirectionalLight()

      app.cameraPostion = [98.23, 47.4, 95.36];
      app.controlsTarget = [0, 2.53, 2.88];

      window.app = app;

      camera = app.camera;
      camera.position.set(...app.cameraPostion);
      scene = app.scene;
      scene.fog = new THREE.Fog('#000000', 100, 680);

      renderer = app.renderer;
      renderer.outputEncoding = THREE.SRGBColorSpace;
      renderer.toneMapping = THREE.ACESFilmicToneMapping;
      renderer.shadowMap.enabled = true;
      renderer.shadowMap.type = THREE.PCFSoftShadowMap;
      controls = app.controls;
      controls.target.set(...app.controlsTarget);
      controls.maxPolarAngle = Math.PI / 2.2;
      clock = new THREE.Clock();

      await loaderModel(app)

      let instance = new cssRender(CSS3DRenderer, app);
      app.cssRender = instance;
      app.instance = instance;

      reprocessing = createReprocessing(app);

      // const geometry = new THREE.BoxGeometry(1, 1, 1);
      // const material = new THREE.MeshStandardMaterial({ color: 0x00ff00 });
      // const cube = new THREE.Mesh(geometry, material);
      // scene.add(cube);

      app.render(()=>{
        let delta = clock.getDelta();
        controls.update(delta)

        scene.traverse(darkenNonBloomed);
        reprocessing.render();
        scene.traverse(restoreMaterial);
        app.finalComposer.render();

        app.cssRender.CSSRender.render(scene,camera)

        if (secondaryTexture) {
          secondaryTexture.offset.y -= 0.001;
        }

        if (trunkTexture) {
            trunkTexture.offset.x -= 0.001;
        }

        if (primaryTexture) {
          primaryTexture.offset.x -= 0.001;
        }

        TWEEN.update()
      })

      function darkenNonBloomed(obj) {
        if (obj.isMesh && app.bloomLayer.test(obj.layers) === false) {
          materials[obj.uuid] = obj.material;
          obj.material = darkMaterial;
        }
      }

      function restoreMaterial(obj) {
        if (materials[obj.uuid]) {
          obj.material = materials[obj.uuid];
          delete materials[obj.uuid];
        }
      }
    }

    onMounted(() => {
      initZThree()
    })

    return {}
  }
}
</script>

