<template>
  <div class="container">
    <form>
        <label v-for="(item,index) in controls" :key="index" @click="activeFun(item, index)">
            <input type="radio" name="radio" :checked="index === activeIndex" />
            <span>{{ item.name }}</span>
        </label>
      </form>
    <!-- 楼层返回 -->
    <div class="back animated fadeIn" @click="backFloorBase" v-if="EventBus.isShowFloorBack">
      <img src="./../assets/image/back.png" alt="" />
      <p>返回</p>
    </div>
  </div>
  <!-- 楼层ui -->
  <layer
    :layers="layerData"
    :active="currentLayer"
    :styles="{ top: '55%', left: '72%', height: '400px' }"
    @change="changeLayer"
    v-if="EventBus.isShowFloorBack"
  ></layer>
  <tooltip
      :style="{
        visibility: state.roomTooltipStyle.show ? 'visible' : 'hidden',
        left: state.roomTooltipStyle.x + 'px',
        top: state.roomTooltipStyle.y + 'px'
      }"
      :data="state.roomTooltipStyle"
    ></tooltip>
</template>

<script>
import { ref, reactive, watch } from 'vue';
import layer from './layer'
import tooltip from './tooltip'

import {loaderFloorManager, setModelLayer} from '../three/floorManager';
import { setModelDefaultMaterial, destroyModelControlGroup } from '../three/loaderModel';
import { cameraUrls, parkData } from '../assets/mock/mock'
import { loadParkElectrion, destoryElectrionModel } from '../three/parkElectrion'
import { loadParkWater, destoryWaterModel } from '../three/parkWater'

export default {
  name: 'bigControl',
  components: {
    layer,
    tooltip
  },
  props: {
    // props
  },
  data() {
    return {
      // data
    };
  },
  setup() {
    const state = reactive({
      // 控制弹出提示窗
      roomTooltipStyle: {
        name: '',
        type: '',
        x: 0,
        y: 0,
        show: false
      }
    })
    let activeIndex = ref(0);
    // 选中某栋楼的数据
    const EventBus = reactive({
      isShowFloorBack: false,
      model: null
    })
    // 在模型内部监听事件获取参数，然后经过监听事件处理返回给state.roomTooltipStyle
    const tooltipInfo = ref({
      name: '',
      type: '',
      x: 0,
      y: 0,
      show: false
    })

    // 电力弹窗数据
    const electrionInfo = ref({
      name: '',
      type: '',
      x: 0,
      y: 0,
      show: false
    })

    // 楼层数据
    const layerData = ref([])
    let currentFloor = ref(0)
    const defaultLayer = '全楼'
    // 显示楼层
    let currentLayer = ref('全楼')
    // 点击四个大按钮实现不同效果
    let controls = reactive([
        {
            name: '首页',
            goFunction: () => {
              window.app.flyTo({
                position: window.app.cameraPostion,
                controls: window.app.controlsTarget,
                duration: 1000
              });
            },
            backFunction: () => {}
        },
        {
            name: '楼层管理',
            goFunction: () => {
              console.log('楼层管理');
              EventBus.isShowFloorBack = false;
              loaderFloorManager(window.app, EventBus)
            },
            backFunction: () => {
              console.log('楼层管理返回');
              if (EventBus.model && currentLayer.value !== '全楼') {
                currentLayer.value = '全楼';
                setModelLayer(window.app, EventBus.model, currentLayer.value, layerData, tooltipInfo)
                setTimeout(() => {
                  setModelDefaultMaterial(window.app);
                }, 1000);
              } else {
                setModelDefaultMaterial(window.app);
              } 
              destroyModelControlGroup(window.app, 'floorText')
              EventBus.isShowFloorBack = false;
              state.roomTooltipStyle.show = false;
            }
        },
        {
            name: '电力监测',
            goFunction: () => {
              loadParkElectrion(window.app, electrionInfo);
            },
            backFunction: () => {
              destoryElectrionModel(window.app, electrionInfo)
              setModelDefaultMaterial(window.app)
            }
        },
        {
            name: '水力监测',
            goFunction: () => {
              loadParkWater(window.app, electrionInfo);
            },
            backFunction: () => {
              destoryWaterModel(window.app, electrionInfo)
              setModelDefaultMaterial(window.app)
            }
        }
    ])
    function activeFun(item, index) {
        // 控制选中状态
        activeIndex.value = index;
    }
    // 返回楼栋管理页面
    function backFloorBase() {
      EventBus.isShowFloorBack = false;
      state.roomTooltipStyle.show = false;

      if (EventBus.model && currentLayer.value !== '全楼') {
        currentLayer.value = '全楼';
        setModelLayer(window.app, EventBus.model, currentLayer.value, layerData, tooltipInfo)
        setTimeout(() => {
          setModelDefaultMaterial(window.app);
          loaderFloorManager(window.app, EventBus)
          EventBus.model = null
        }, 1000);
      } else {
        setModelDefaultMaterial(window.app);
        loaderFloorManager(window.app, EventBus)
      } 
    }

    // 选中楼层事件
    function changeLayer(layer) {
      currentLayer.value = layer
      setModelLayer(window.app, EventBus.model, layer, layerData, tooltipInfo)
    }

    // 监听路由变化，控制功能展示跳转
    watch(activeIndex, (newValue, oldValue) => {
      // 控制页面跳转
      const oldControl = controls.find(item => item.name === controls[oldValue]?.name);
      if (oldControl) {
        oldControl.backFunction();
      }
      const newControl = controls.find(item => item.name === controls[newValue]?.name);
      if (newControl) {
        newControl.goFunction();
      }
    })
    
    // 监听楼层ui点击事件，页面上显示的楼层ui做对应高亮
    watch(EventBus, (newValue) => {
      if (newValue && newValue.model) {
        const layerNames = EventBus.model.children.filter(item => item.name.indexOf('F')> -1).map(item => item.name.substring(0,item.name.indexOf('F') + 1))
        layerData.value = [defaultLayer].concat(layerNames)
      }
    })

    // 监听房间弹窗数据变化，给当前页面弹窗数据转换后赋值
    watch(tooltipInfo, (newValue) => {
      if (newValue) {
        if(newValue.name.indexOf('摄像头') > -1) {
          state.roomTooltipStyle = Object.assign({
            楼栋: EventBus.model.name,
            楼层: currentLayer.value,
            摄像头: newValue.name,
            视频: cameraUrls[newValue.name.substring(0, 4)]
          }, newValue)
        } else {
          const roomName = newValue.name.substring(0, 3)
          state.roomTooltipStyle = Object.assign({
            楼栋: EventBus.model.name,
            楼层: currentLayer.value,
            房间号: newValue.name,
            度数: parkData[EventBus.model.name][currentLayer.value][roomName][newValue.type]
          }, newValue)
        }
      }
    })

    // 监听电力弹窗数据变化，给当前页面弹窗数据转换后赋值
    watch(electrionInfo, (newValue) => {
      if (newValue) {
        state.roomTooltipStyle = newValue
      }
    })

    return {
        activeFun,
        activeIndex,
        controls,
        EventBus,
        backFloorBase,
        changeLayer,
        currentLayer,
        currentFloor,
        layerData,
        tooltip,
        tooltipInfo,
        state
    }
  }
};
</script>

<style scoped>
/* scoped style */
.container {
  position:fixed;
  z-index: 999;
}

.back {
  width: 48px;
  position: fixed;
  bottom: 4%;
  left: 25%;
  z-index: 3;
  cursor: pointer;
  font-size: 18px;
  img {
    width: 100%;
  }
  p {
    color: #fff;
    text-align: center;
  }
}
.container form {
  display: flex;
  flex-wrap: wrap;
}

.container label {
  display: flex;
  cursor: pointer;
  font-weight: 500;
  position: relative;
  overflow: hidden;
  margin-bottom: 0.375em;
}

.container label input {
  position: absolute;
  left: -9999px;
}

.container label input:checked + span {
  background-color: #414181;
  color: white;
}

.container label input:checked + span:before {
  box-shadow: inset 0 0 0 0.4375em #00005c;
}

.container label span {
  display: flex;
  align-items: center;
  padding: 0.375em 0.75em 0.375em 0.375em;
  border-radius: 99em;
  transition: 0.25s ease;
  color: #414181;
}

.container label span:hover {
  background-color: #d6d6e5;
}

.container label span:before {
  display: flex;
  flex-shrink: 0;
  content: '';
  background-color: #fff;
  width: 1.5em;
  height: 1.5em;
  border-radius: 50%;
  margin-right: 0.375em;
  transition: 0.25s ease;
  box-shadow: inset 0 0 0 0.125em #00005c;
}
</style>
