import * as THREE from 'three';
import { secondaryMaterial, trunkMaterial, primaryMaterial } from './material';

// 加载模型
export function loaderModel(app) {
    return new Promise((resolve) => {
        app.controlGroup = new THREE.Group()
        app.scene.add(app.controlGroup)
        app.modelMaterials = {}
        app.rayModel = []
        let urls = [
            {
                type: 'rgbe',
                url: 'texture/spree_bank_1k.hdr',
                onload: (texture) => {
                  const pmremGenerator = new THREE.PMREMGenerator(app.renderer);
                  pmremGenerator.compileEquirectangularShader();
                  app.envMap = pmremGenerator.fromEquirectangular(texture).texture;
                }
            },
            {
                type: 'gltf',
                url: 'model/model4.glb',
                onload: (object) => {
                    console.log('模型加载完成:');
                    console.log(object);
                    app.scene.add(object.scene);

                    app.model = object.scene;

                    const receiverShadow = ['路面']
                    const bloomModels = ['楼间隔', '承重柱', '玻璃', '路灯面', '停车线', '顶亮面', '车流线'];
                    app.model.traverse((child) => {
                        if (child.material) {
                            app.modelMaterials[child.name] = {
                                material: child.material,
                            }
                            
                            if (child.name === '其余建筑_1') {
                                child.material.envMap = app.envMap;
                            }
                        }

                        let {x,y,z} = child.position;
                        child.position_tmp = {x,y,z}

                        // 接收投影
                        if (receiverShadow.includes(child?.parent?.name)) {
                            child.receiveShadow = true
                        } else {
                            child.castShadow = true
                            child.receiveShadow = false
                        }
                        
                        // 设置车流线的材质，准备设置可动
                        if (child.name.indexOf('车流线1') > -1) {
                            child.material = secondaryMaterial;
                          }
              
                          if (child.name.indexOf('车流线2') > -1) {
                            child.material = trunkMaterial;
                          }
              
                          if (child.name.indexOf('车流线3') > -1) {
                            child.material = primaryMaterial;
                          }

                        // 设置可以被后处理检测到
                        for (let i = 0; i < bloomModels.length; i++) {
                            const value = bloomModels[i];
                            if (child.name.indexOf(value) > -1) {
                              child.layers.enable(1);
                              break;
                            } else {
                              child.layers.enable(0);
                            }
                          }
                    })
                }
            }
        ]

        app.iterateLoad(urls, null, ()=>{}, ()=>{console.log('模型加载出错')})

        resolve()
    })
}

// 所有的材质返回默认的材质
export function setModelDefaultMaterial(app) {
    
    for(let j = 0; j < app.model.children.length; j++) {
        const obj = app.model.children[j];
        obj.traverse((childObj)=>{
            if (childObj.material) {
                childObj.material = app.modelMaterials[childObj.name].material
            }
        })
    }
}

// 移除指定名称的3D HTML标签
export function destroyModelControlGroupText(app, className) {
    const textDom = document.getElementsByClassName(className)
    for(let i = 0; i < textDom.length; i++) {
        textDom[i].onclick = null;
    }
    app.instance.removeAll(app.controlGroup);
}

// 返回初始，还原控制内容
export function destroyModelControlGroup(app, className) {
    if (app?.controlGroup?.children?.length === 0) {
        return;
    }

    if (className) {
        destroyModelControlGroupText(app, className)
    }

    for (let i = app.controlGroup.children.length - 1; i >= 0; i--) {
        let e = app.controlGroup.children[i]
        if (e.isMesh) {
            e.geometry.dispose()
            e.material.dispose()
            app.controlGroup.remove(e)
        }
    }
}