import {CSS3DSprite} from 'three/examples/jsm/renderers/CSS3DRenderer'
import {floorBaseMaterial} from './material'
import { destroyModelControlGroupText } from './loaderModel'
import { roomTexts, parkData } from '../assets/mock/mock'

// 点击楼层管理
export function loaderFloorManager(app, EventBus) {
    app.flyTo({
        position: [0,35,113],
        controls: [0,0,0],
        done: () => {
            createFloorText(app, EventBus)
        }

    })
}

// 创建提示楼栋文本
export function createFloorText(app, EventBus) {
    app.model.traverse(function (obj) {
        if (obj.name.indexOf('楼顶')>-1) {
            const name = obj.parent.name
            const position = Object.values(app.getModelWorldPosition(obj))
            const html = `<div class="floorText-3d animated fadeIn" id="${name}"><p class="text">${name}</p></div>`
            app.instance.add({
                element: html,
                position,
                scale: [0.05,0.05,0.05],
                name,
                parent: app.controlGroup,
                cssObject: CSS3DSprite
            })
        }
    })

    const textDoms = document.getElementsByClassName('floorText-3d');
    for (let i = 0; i < textDoms.length; i++) {
        const textDom = textDoms[i];
        
        textDom.onclick = () => {
            destroyModelControlGroupText(app, 'floorText-3d')
            for(let j = 0; j < app.model.children.length; j++) {
                const obj = app.model.children[j];
                if (obj.name === textDom.id) {
                    EventBus.isShowFloorBack = true
                    EventBus.model = obj
                    const centerPosition = Object.values(app.getModelWorldPosition(obj))
                    app.flyTo({
                        position: [centerPosition[0] + 40, centerPosition[1] + 50, centerPosition[2] + 40],
                        controls: centerPosition
                    })

                    obj.traverse((childObj)=>{
                        if (childObj.material) {
                            childObj.material = app.modelMaterials[childObj.name].material
                        }
                    })
                } else {
                    obj.traverse((childObj)=>{
                        if (childObj.material) {
                            childObj.material = floorBaseMaterial
                        }
                    })
                }

            }
        }
    }
}

//创建房间文本
export function createRoomText(app, model, tooltip) {
    model.traverse(function (obj) {
        if (obj.isMesh) {
            roomTexts.forEach((item) => {
                if (obj.name.indexOf(item.name) > -1) {
                    const name = obj.name
                    const position = Object.values(app.getModelWorldPosition(obj))
                    const html = `<div class="room-3d animated fadeIn" id="${name}" _type="${item.type}"><p class="text">${name}</p><div class="${item.class}"></div></div>`
                    app.instance.add({
                        element: html,
                        position,
                        scale: [0.01,0.01,0.01],
                        name,
                        parent: app.controlGroup,
                        cssObject: CSS3DSprite
                    })
                }
            })
        }
    })

    const textDoms = document.getElementsByClassName('room-3d');
    for (let i = 0; i < textDoms.length; i++) {
        const textDom = textDoms[i];
        textDom.onclick = (event) => { 
            const type = textDom.getAttribute('_type')
            const model = app.model.getObjectByName(textDom.id)
            tooltip.value = {
                name: model.name,
                type: type,
                x: event.x,
                y: event.y,
                show: tooltip.value.name === model.name ? !tooltip.value.show : true
            }
            console.log(parkData)
        }
    }
}

// 设置点击某一层展开或者闭合
export function setModelLayer(app, model, layerName, layerData, tooltip, callback) {
    destroyModelControlGroupText(app, 'room-3d')
    destroyModelControlGroupText(app, 'floorText-3d')
    const currentLayer = Number(layerName.substr(0, layerName.indexOf('F')))
    for (let i = 0; i < model.children.length; i++) {
        const mesh = model.children[i];
        const name = mesh.name;
        let num;
        if (name.indexOf('楼顶')>-1) {
            num = layerData.value.length + 1;
        } else {
            num = Number(name.substr(0, layerName.indexOf('F')))
        }

        if(!isNaN(num)) {
            let value = num - currentLayer;
            let position = mesh.position;
            let position_tmp = mesh.position_tmp;
            let toPosition;
    
            if(layerName === '全楼') {
                toPosition = [position_tmp.x, position_tmp.y, position_tmp.z]
            } else {
                if (value >= 1) {
                    toPosition = [position_tmp.x, position_tmp.y + 20, position_tmp.z]
                } else {
                    toPosition = [position_tmp.x, position_tmp.y, position_tmp.z]
                }
            }
            app.modelMove({
                fromPosition: [position.x, position.y, position.z],
                toPosition,
                duration: 1000,
                done: () => {
                    if (layerName === '全楼') {
                        if (callback) {
                            callback();
                            return;
                        }
                        const centerPosition = Object.values(app.getModelWorldPosition(model))
                        app.flyTo({
                            centerPosition: [centerPosition[0] + 40, centerPosition[1] + 50, centerPosition[2] + 40],
                            controls: centerPosition
                        })
                        return;
                    }

                    if (mesh.name.indexOf(layerName)>-1){
                            if (callback) {
                                callback();
                                return;
                            }
                            const centerPosition = Object.values(app.getModelWorldPosition(mesh))
                            app.flyTo({
                                centerPosition: [centerPosition[0] + 4, centerPosition[1] + 10, centerPosition[2] + 15],
                                controls: centerPosition,
                                done: () => {
                                    createRoomText(app, mesh, tooltip)
                                }
                            })
                            return;
                        }
                }
            }, mesh)
        } 
    }
}
