/**
 * 该函数用于初始化一个CSSRender实例，并将其附加到提供的`app`元素上。
 *
 * 该函数执行以下操作：
 * 1. 创建一个新的CSSRender实例，并将其大小设置为与app元素的大小相匹配。
 * 2. 将CSSRender的domElement的位置设置为绝对，并将top设置为0px。
 * 3. 将CSSRender的domElement的pointerEvents设置为'none'。
 * 4. 将CSSRender的domElement附加到app元素上。
 * 5. 将`this`实例的`CSSRender`和`cssRenderDomElement`属性设置为新创建的CSSRender和其domElement。
 */
export function cssRender(CSSRender, app) {
    const T = this;
    T.config = {};

    /**
     * 初始化CSSRender实例并将其附加到app元素上。
     */
    T.init = function () {
        // 创建一个新的CSSRender实例，并将其大小设置为与app元素的大小相匹配。
        const cssRender = new CSSRender();
        cssRender.setSize(app.element.offsetWidth, app.element.offsetHeight);

        // 将CSSRender的domElement的位置设置为绝对，并将top设置为0px。
        cssRender.domElement.style.position = 'absolute';
        cssRender.domElement.style.top = '0px';

        // 将CSSRender的domElement的pointerEvents设置为'none'。
        cssRender.domElement.style.pointerEvents = 'none';

        // 将CSSRender的domElement附加到app元素上。
        app.element.appendChild(cssRender.domElement);

        // 将`this`实例的`CSSRender`和`cssRenderDomElement`属性设置为新创建的CSSRender和其domElement。
        T.CSSRender = cssRender;
        T.cssRenderDomElement = cssRender.domElement;
    }
    /**
     * 将传入的选项添加到应用程序中。
     *
     * @param {Object|Array} option - 要添加的选项，可以是单个对象或对象的数组。
     */
    T.add = function(option) {
        // 初始化一个空数组来存储选项。
        let list = []

        // 检查传入的选项是否是数组，如果是，则将其赋值给`list`，否则将传入的选项添加到`list`中。
        if (Array.isArray(option)) list = option
        else list.push(option)

        // 遍历`list`中的每个选项，并将其插入到`document.body`的末尾。
        // 然后，创建一个新的`cssObject`实例，并将其添加到应用程序的场景中。
        // 将选项的名称、位置和缩放设置应用到新创建的对象上。
        // 最后，将新创建的对象添加到`T.config`对象中，以便以后使用。
        list.forEach(item => {
            document.body.insertAdjacentHTML('beforeend', item.element)
            const label = new item.cssObject(document.body.lastChild)
            label.userData.isCss23D = true;
            label.position.set(item.position[0], item.position[1], item.position[2])
            label.name = item.name
            if (item.scale) {
                label.scale.set(item.scale[0], item.scale[1], item.scale[2])
            }
            item.parent ? item.parent.add(label) : app.scene.add(label)
            T.config[item.name] = label
        })
    }

    T.removeAll = function(parent) {
        for (let i = parent.children.length - 1; i >= 0; i--) {
            let e = parent.children[i]
            let name = e.name
            if (e.userData.isCss23D) {
                parent.remove(e)
                if (T.config[name]) {
                    delete T.config[name]
                }
            }
        }
    }

    T.init()
}