import * as THREE from 'three';
import { GUI } from "dat.gui";
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { DRACOLoader } from 'three/examples/jsm/loaders/DRACOLoader.js'
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js'
import TWEEN from 'three/examples/jsm/libs/tween.module.js';

/**
 * 代表一个Three.js场景的类。
 */
export default class ZThree {
    /**
     * 创建一个新的ZThree实例。
     * @param {string} id - 场景附加到的HTML元素的ID。
     */
    constructor(id) {
        // 设置id和element属性
        this.id = id;
        this.element = document.getElementById(id);
    }

    /**
     * 初始化Three.js场景、相机、渲染器和辅助对象。
     */
    initZThree() {
        // 初始化场景
        this.scene = new THREE.Scene();

        // 初始化纹理加载器
        this.TextureLoader = new THREE.TextureLoader();

        // 初始化相机
        this.camera = new THREE.PerspectiveCamera(
            45,
            this.element.offsetWidth / this.element.offsetHeight,
            0.1,
            1000
        );
        this.camera.position.set(100, 100, 100);
        this.camera.lookAt(new THREE.Vector3(0, 0, 0));

        // 初始化渲染器
        this.renderer = new THREE.WebGLRenderer({
            antialias: true,
            alpha: true,
            shadowMap: {
                enabled: true,
                type: THREE.PCFSoftShadowMap
            }
        });

        // 设置像素比和渲染器的大小
        this.renderer.setPixelRatio(window.devicePixelRatio);
        this.renderer.setSize(this.element.offsetWidth, this.element.offsetHeight);

        // 将渲染器附加到HTML元素
        this.element.appendChild(this.renderer.domElement);

        // 初始化GUI
        this.gui = new GUI();

        // 添加窗口大小变化的事件监听器
        window.addEventListener("resize", () => {
            // 更新窗口大小时相机和渲染器的比例
            this.camera.aspect =
                this.element.offsetWidth / this.element.offsetHeight;
            this.camera.updateProjectionMatrix();
            this.renderer.setSize(
                this.element.offsetWidth,
                this.element.offsetHeight
            );

            // 如果存在CSS渲染器，则更新其大小
            if (this.cssRenderer) {
                this.cssRenderer.setSize(
                    this.element.offsetWidth,
                    this.element.offsetHeight
                );
            }
        });
    }

    /**
     * 初始化辅助对象（坐标轴辅助对象）。
     */
    initHelper() {
        // 将坐标轴辅助对象添加到场景中
        this.scene.add(new THREE.AxesHelper(100));
    }

    /**
     * 渲染场景。
     * @param {Function} callback - 要执行的回调函数。
     */
    render(callback) {
        // 调用提供的回调函数
        callback();

        // 请求下一帧动画并递归调用render
        this.frameId = requestAnimationFrame(() => this.render(callback));
    }

    /**
     * 初始化相机的轨道控制器。
     * 轨道控制器使得相机可以使用鼠标和触摸输入进行控制。
     */
    initControl() {
        // 使用相机和渲染器的domElement创建一个新的轨道控制器实例
        this.controls = new OrbitControls(this.camera, this.renderer.domElement);

        // 启用平滑的相机移动
        this.controls.enableDamping = true;

        this.controls.enableZoom = true;
        this.controls.autoRotate = false;
        this.controls.autoRotateSpeed = 0.4;
        this.controls.enablePan = true;
    }

    // 加载射线事件
    initRaycaster(callback, models = this.scene.children, eventName = 'click') {
        this.raycaster = new THREE.Raycaster();
        // this好像指错了，用bind强行绑定进去
        this.rayFn = this.rayEventFn.bind(this, models, callback);
        // 绑定监听事件
        this.element.addEventListener(eventName, this.rayFn);
    }

    // 获取鼠标坐标准备指向射线方向的模型
    rayEventFn(models, callback){
        let event = window.event;
        let mouse = {
            x: (event.offsetX / this.element.offsetWidth) * 2 - 1,
            y: -(event.offsetY / this.element.offsetHeight) * 2 + 1
        }

        let activeObj = this.fireRaycaster(mouse, models);

        if (callback) {
            callback(activeObj, this, event, mouse);
        }

        // 设置鼠标变为手型
        // document.body.style.cursor = 'pointer'
    }

    // 获取射线对应坐标的模型
    fireRaycaster(mouse, models) {
        this.raycaster.setFromCamera(mouse, this.camera);
        let intersects = this.raycaster.intersectObjects(models, true);

        if (intersects.length > 0) {
            let selectObject = intersects[0];
            return selectObject;
        } else {
            return false
        }
    }

    // 移除监听
    destroyRaycaster(eventName) {
        this.raycaster = null;
        this.element.removeEventListener(eventName, this.rayFn);
    }

    /**
     * 加载环境光源
     */
    initAmbientLight() {
        const light = new THREE.AmbientLight(0x404040);
        this.scene.add(light);
    }

    /**
     * 加载平行光源
     */
    initDirectionalLight() {
        const light = new THREE.DirectionalLight(0xffffff, 0.5);
        // 设置平行光源的位置
        light.position.set(100, 300, -100);

        // 允许该平行光源投射阴影
        light.castShadow = true;

        // 设置阴影图的分辨率
        light.shadow.mapSize.width = 1024;
        light.shadow.mapSize.height = 1024;

        // 配置用于阴影的相机参数
        light.shadow.camera.near = 0.5; // 阴影相机的最近距离
        light.shadow.camera.far = 500; // 阴影相机的最远距离
        light.shadow.camera.left = -100; // 阴影相机的左平面
        light.shadow.camera.right = 100; // 阴影相机的右平面
        light.shadow.camera.top = 100; // 阴影相机的上平面
        light.shadow.camera.bottom = -100; // 阴影相机的下平面

        // 设置阴影的偏移和法线偏移
        light.shadow.bias = 0.05; // 避免阴影效果上的杂色
        light.shadow.normalBias = 0.05; // 用于非均匀表面上的阴影
        this.scene.add(light);

        // const helper = new THREE.DirectionalLightHelper(light,5);
        // this.scene.add(helper);
    }

    /**
     * 加载模型
     */
    loadModel(option) {
        switch (option.type) {
            case 'rgbe':
                if (!this.rgbeLoader) {
                    this.rgbeLoader = new RGBELoader();
                }
                this.rgbeLoader.load(option.url, option.onload, option.onProgress, option.onError);
                break;
            case "gltf":
            case "glb":
                if (!this.gltfLoader) {
                    this.gltfLoader = new GLTFLoader();
                    let dracoLoader = new DRACOLoader();
                    dracoLoader.setDecoderPath("assets/draco/");
                    this.gltfLoader.setDRACOLoader(dracoLoader);
                }
                this.gltfLoader.load(option.url, option.onload, option.onProgress, option.onError);
                break;
            default:
                break;
        }
    }

    // 模型加载事件和状态赋予
    iterateLoad(objFileList, onProgress, onAllLoad, onError) {
        let fileIndex = 0;
        let that = this;

        function interateLoadForFile() {
            that.loadModel({
                type: objFileList[fileIndex].type,
                url: objFileList[fileIndex].url,
                onload: function(object){
                    if (objFileList[fileIndex].onload) {
                        objFileList[fileIndex].onload(object);
                    }
                    fileIndex++;
                    if (fileIndex < objFileList.length) {
                        interateLoadForFile();
                    } else {
                        onAllLoad();
                    }
                },
                onProgress: function(xhr){
                    if (objFileList[fileIndex].onProgress) {
                        objFileList[fileIndex].onProgress(xhr);
                    }
                    if (onProgress) {
                        onProgress(xhr, fileIndex);
                    }
                },
                onError: function(xhr){
                    if (objFileList[fileIndex].onError) {
                        objFileList[fileIndex].onError(xhr);
                    }
                    if (onError) {
                        onError(xhr, fileIndex);
                    }
                }
            });
        }

        interateLoadForFile()
    }

    // 镜头移动
    flyTo(option) {
        option.position = option.position || []
        option.controls = option.controls || []
        option.duration = option.duration || 1000
        option.easing = option.easing || TWEEN.Easing.Linear.None

        const curPosition = this.camera.position
        const curControls = this.controls.target
        
        const tween = new TWEEN.Tween({
            x1: curPosition.x,
            y1: curPosition.y,
            z1: curPosition.z,
            x2: curControls.x,
            y2: curControls.y,
            z2: curControls.z
        }).to({
            x1: option.position[0],
            y1: option.position[1],
            z1: option.position[2],
            x2: option.controls[0],
            y2: option.controls[1],
            z2: option.controls[2],
        }, option.duration).easing(option.easing)

        tween.onStart(() => {
            this.controls.enabled = false
            if (option.start instanceof Function) {
                option.start()
            }
        })

        tween.onUpdate(() => {
            this.controls.enabled = false
            this.camera.position.set(tween._object.x1, tween._object.y1, tween._object.z1)
            this.controls.target.set(tween._object.x2, tween._object.y2, tween._object.z2)
            this.controls.update()
            if (option.update instanceof Function) {
                option.update()
            }
        })

        tween.onComplete(() => {
            this.controls.enabled = true
            if (option.done instanceof Function) {
                option.done()
            }
        })

        tween.onStop(() => {
            this.controls.enabled = true
            if (option.stop instanceof Function) {
                option.stop()
            }
        })

        tween.start()
        TWEEN.add(tween)
        return tween
    }

    modelMove(option, obj) {
        option.fromPosition = option.fromPosition || []
        option.toPosition = option.toPosition || []
        option.duration = option.duration || 1000
        option.easing = option.easing || TWEEN.Easing.Linear.None

        const tween = new TWEEN.Tween({
            x1: option.fromPosition[0],
            y1: option.fromPosition[1],
            z1: option.fromPosition[2]
        }).to({
            x1: option.toPosition[0],
            y1: option.toPosition[1],
            z1: option.toPosition[2],
        }, option.duration).easing(option.easing)

        tween.onStart(() => {
            this.controls.enabled = false
            if (option.start instanceof Function) {
                option.start()
            }
        })

        tween.onUpdate(() => {
            this.controls.enabled = false
            obj.position.set(tween._object.x1, tween._object.y1, tween._object.z1)
            if (option.update instanceof Function) {
                option.update()
            }
        })

        tween.onComplete(() => {
            this.controls.enabled = true
            if (option.done instanceof Function) {
                option.done()
            }
        })

        tween.onStop(() => {
            this.controls.enabled = true
            if (option.stop instanceof Function) {
                option.stop()
            }
        })

        tween.start()
        TWEEN.add(tween)
        return tween
    }

    // 获取世界坐标
    getModelWorldPosition(model){
        this.scene.updateMatrixWorld()
        const worldPosition = new THREE.Vector3()
        model.getWorldPosition(worldPosition)
        return worldPosition
    }
    
}
