import {floorBaseMaterial2} from './material'
import { parkData } from '../assets/mock/mock';
import { ElNotification } from 'element-plus';

export function loadParkWater(app, electrionInfo) {
    app.flyTo({
        position: [53, 68.1, 132.1],
        controls: [-3.85, -12.06, 8.96],
        done: () => {
            createWaterModel(app, electrionInfo);
        }
    });
}

export function createWaterModel(app, electrionInfo) {
    let notifIndex = 0;
    app.model.traverse((obj) => {
        if (obj.isMesh) {
            if (obj.name.indexOf('水管') === -1) {
                obj.material = floorBaseMaterial2;
            } else {
                // 获取到每一个水管的值
                const floorName = obj.parent.parent.name; // 楼栋信息
                const layerName = obj.parent.name.substr(0, 2); // 楼层信息
                const roomName = obj.name.substr(0, 3); // 房间信息
                const value = parkData[floorName][layerName][roomName]['水'];
                // 定义阈值
                if (value > 15) {
                    app.selectedObjects.push(obj);
                    if (notifIndex < 6) {
                        setTimeout(() => {
                            ElNotification({
                                title: '警告',
                                message: `${floorName}的${layerName}${roomName}的用水量为${value}度, 已超过平均标准，请留意`,
                                type: 'warning',
                                duration: 6000
                            });
                        }, notifIndex * 200);
                        notifIndex++;
                    }
                    app.rayModel.push(obj);
                }
            }
        }
    });
    app.initRaycaster((activeObj, app, event)=>{
        if(activeObj) {
            const obj = activeObj.object;
            const floorName = obj.parent.parent.name; // 楼栋信息
            const layerName = obj.parent.name.substr(0, 2); // 楼层信息
            const roomName = obj.name.substr(0, 3); // 房间信息
            const value = parkData[floorName][layerName][roomName]['水'];

            electrionInfo.value = {
                楼栋: floorName,
                楼层: layerName,
                房间号: roomName,
                度数: value,
                name: obj.name,
                type: '水',
                x: event.x,
                y: event.y,
                show: true
            }
        } else {
            electrionInfo.value.show = false
        }
    },app.rayModel, 'click')
}

export function destoryWaterModel(app, electrionInfo) {
    app.selectedObjects = [];
    app.outlinePass.selectedObjects = app.selectedObjects;
    electrionInfo.value.show = false;
    app.destroyRaycaster()
}